  word-wrap: break-word;
}

.sec ul {
  margin: 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  list-style: none;
}
.sec li {
  padding: 10px;
  width: 300px;
  transition: box-shadow 0.3s;
}

.hover {
  box-shadow: 0 12px 10px 0 rgba(165, 34, 34, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.hover span {
  color: blue;
}
.hover p {
  color: #555;
}
.hover h3 {
  color: blue;
}
.hover h3:after {
  width: 25px;
}

.before,
.after {
  position: static;
}

.sec h3 {
  color: darken(green, 20%);
  display: inline-block;
  font-size: 1.875em;
  font-weight: 100;
  transition: color 0.3s;
}
.after {
  content: "";
  position: relative;
  left: 50%;
  top: 10px;
  transform: translateX(-50%);
  display: block;
  width: 40px;
  height: 1px;
  background: darken(green, 20%);
  transition: width 0.3s;
}

.sec p {
  color: whitesmoke;
  text-align: center;
  text-indent: 1em;
  font-size: 1.25em;
  line-height: 20px;
  transition: color 0.3s;
}

